import React, {FC} from 'react';
import {
  Cell,
  Grid,
  Offset,
  Row,
  Section,
  Spacer,
  Accordion,
} from '@excelwithbusiness/webmobile-sc-components';
// @ts-ignore-next-line
import {isArray, length} from '@/utils/FP/FP';
import {prepareEntries} from "./helpers";

export const Content = ({slices}) => {
    if (!isArray(slices) || !length(slices, 0)) {
        return null;
    }

    return (
        <Section>
            {slices?.map((slice: any, index: number) => {
                const keyProp = `${slice?.type}_${index}`;
                switch (slice?.type) {
                    case 'Accordion':
                        return (
                            <Grid>
                                <Row>
                                    <Offset colsXs={0} columns={1} />
                                    <Cell columns={10}>
                                        <Spacer size="md" />
                                        <Accordion entries={prepareEntries(slice)} isLarge withIconsOnRight />
                                    </Cell>
                                </Row>
                            </Grid>
                        );
                    default:
                        return null;
                }
            })}
        </Section>
    )
};
